* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

span {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  margin: 0 auto;
  width: 1027px;
}

.hidden {
  display: none;
}

@font-face {
  font-family: "SF Compact Display";
  src: url(../assets/font/sf-compact-display-medium-5864711817c30.otf);
}
.key {
  font-family: "SF Compact Display", sans-serif;
  font-size: 22px;
  line-height: 26px;
  color: #FFFFFF;
}

._control {
  font-family: "SF Compact Display", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #FFFFFF;
}

.keyboard {
  display: flex;
  align-content: flex-start;
  gap: 8px 8px;
  width: 1027px;
  height: 352px;
  padding: 10px;
  flex-flow: row wrap;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0.2) 14.52%, rgba(255, 255, 255, 0) 87.38%), #626166;
  box-shadow: 0px 2px 0px #191A1F;
  border-radius: 12px;
}

.key {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 62px;
  height: 60px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.06) 100%), #191A1F;
  border-radius: 6px;
  cursor: pointer;
}

.active {
  border: 2px solid #37fff8;
}

._control {
  align-items: flex-end;
  justify-content: center;
  padding-bottom: 11px;
}

.arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-end;
  min-width: 62px;
  height: 31px;
  background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.06) 100%), #191A1F;
  border: 2px solid #000000;
  border-radius: 6px;
}
.arrow-up {
  border-radius: 6px 6px 0 0;
  height: 30px;
  box-shadow: inset 0px -3px 1px rgba(0, 0, 0, 0.45), inset 0px -6px 2px rgba(255, 255, 255, 0.25), inset 0px 3px 1px rgba(255, 255, 255, 0.15);
}
.arrow-down {
  height: 30px;
  border-radius: 0 0 6px 6px;
}

.caps_enter {
  width: 114px;
}

.tab_backspace {
  width: 97px;
}

._shift {
  width: 149px;
}

._command {
  width: 79px;
}

._space {
  width: 342px;
}

.arrow-middle {
  max-width: 60px;
  box-sizing: border-box;
}

#ArrowRight {
  background-image: url(../assets/image/arrow.svg);
  background-repeat: no-repeat;
  background-position: 50%;
  transform: rotate(180deg);
}

#ArrowLeft {
  background-image: url(../assets/image/arrow.svg);
  background-repeat: no-repeat;
  background-position: 50%;
}

.textarea {
  width: 100%;
  min-height: 100px;
}